import nacl from 'tweetnacl';
import { encodeBase64, decodeBase64 } from 'tweetnacl-util';

/**
 * 
 * PBKDF2-like
 */
export function deriveKeyFromSeed(seed) {
  try {
    // Base64
    const seedBytes = decodeBase64(seed);
    
    // SHA512()32
    // : TweetNaClKDFsecretbox
    const hash = nacl.hash(seedBytes);
    const key = hash.slice(0, 32); // 32
    
    return key;
  } catch (e) {
    console.error(':', e);
    throw new Error('');
  }
}

/**
 *  (ChaCha20Poly1305 via NaCl secretbox)
 */
export function encryptData(message, key) {
  try {
    // nonce
    const nonce = nacl.randomBytes(24);
    
    // 
    const messageBytes = new TextEncoder().encode(message);
    
    // secretbox
    const encrypted = nacl.secretbox(messageBytes, nonce, key);
    
    //  nonce + ciphertext (Base64)
    return {
      nonce: encodeBase64(nonce),
      ciphertext: encodeBase64(encrypted)
    };
  } catch (e) {
    console.error(':', e);
    throw new Error('');
  }
}

/**
 * 
 */
export function decryptData(encrypted, key) {
  try {
    const nonce = decodeBase64(encrypted.nonce);
    const ciphertext = decodeBase64(encrypted.ciphertext);
    
    const decrypted = nacl.secretbox.open(ciphertext, nonce, key);
    if (!decrypted) {
      throw new Error('');
    }
    
    return new TextDecoder().decode(decrypted);
  } catch (e) {
    console.error(':', e);
    throw new Error('');
  }
}

/**
 * / ()
 */
export function generateKeyPair() {
  const keyPair = nacl.sign.keyPair();
  return {
    publicKey: encodeBase64(keyPair.publicKey),
    secretKey: encodeBase64(keyPair.secretKey)
  };
}
