import React, { useState } from 'react';
import { getStoredConfig, saveConfig, clearConfig, SERVER_HOST, SERVER_PORT, SERVER_URL } from '../config';
import '../styles/ServerConfig.css';

function ServerConfig({ onConfigSaved }) {
  const [host, setHost] = useState(SERVER_HOST);
  const [port, setPort] = useState(SERVER_PORT);
  const [message, setMessage] = useState('');
  const [isEditing, setIsEditing] = useState(false);

  const handleSave = () => {
    if (!host || !port) {
      setMessage(' ');
      return;
    }

    if (isNaN(port) || port < 1 || port > 65535) {
      setMessage('  1-65535 ');
      return;
    }

    const success = saveConfig(host, parseInt(port, 10));
    if (success) {
      setMessage(' ');
      setIsEditing(false);
      onConfigSaved && onConfigSaved();
    } else {
      setMessage(' ');
    }
  };

  const handleReset = () => {
    clearConfig();
    setHost('localhost');
    setPort(3001);
    setMessage(' ');
    setIsEditing(false);
  };

  const handleCancel = () => {
    setHost(SERVER_HOST);
    setPort(SERVER_PORT);
    setMessage('');
    setIsEditing(false);
  };

  return (
    <div className="server-config">
      <div className="config-header">
        <h3></h3>
        <button
          className="toggle-btn"
          onClick={() => setIsEditing(!isEditing)}
        >
          {isEditing ? '' : ''}
        </button>
      </div>

      {!isEditing ? (
        <div className="config-display">
          <div className="config-item">
            <span className="label">:</span>
            <span className="value">{SERVER_URL}</span>
          </div>
        </div>
      ) : (
        <div className="config-form">
          <div className="form-group">
            <label>:</label>
            <input
              type="text"
              value={host}
              onChange={(e) => setHost(e.target.value)}
              placeholder=": localhost  192.168.1.100"
            />
          </div>

          <div className="form-group">
            <label>:</label>
            <input
              type="number"
              value={port}
              onChange={(e) => setPort(e.target.value)}
              min="1"
              max="65535"
              placeholder=": 3001"
            />
          </div>

          <div className="config-preview">
            <span className="label">:</span>
            <span className="value">http://{host}:{port}</span>
          </div>

          {message && (
            <div className={`message ${message.includes('') ? 'success' : 'error'}`}>
              {message}
            </div>
          )}

          <div className="button-group">
            <button className="btn btn-primary" onClick={handleSave}>
              
            </button>
            <button className="btn btn-secondary" onClick={handleReset}>
              
            </button>
            <button className="btn btn-cancel" onClick={handleCancel}>
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default ServerConfig;
